* {
  box-sizing: border-box; }

html, body {
  font-family: 'Open Sans', sans-serif; }

html {
  font-size: 10px; }

body {
  font-size: 16px; }

fieldset {
  border: none; }

.f-order, .order-wrap {
  background-color: #fff; }

.f-order__title, .order-wrap__title {
  margin: 0;
  font-size: 18px;
  color: #231f20;
  line-height: 1.778; }

.f-order__input, .f-order__select, .f-order__textarea {
  width: calc(100% - 15rem);
  max-width: 36rem;
  border-radius: 5px;
  border: 1px solid #ccc; }
  .f-order__input:focus, .f-order__select:focus, .f-order__textarea:focus {
    outline: none;
    border-color: #3882B9;
    box-shadow: 0 0 10px #3882B9; }

.order__link, .btn-default {
  color: #3882B9;
  font-size: 1.4rem;
  text-decoration: underline;
  font-weight: bold; }
  .order__link:hover, .btn-default:hover {
    opacity: .8; }

.dark-bg {
  background-color: #F9F7EE; }

.container {
  max-width: 123rem;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start; }

.section__title {
  width: 100%;
  font-size: 3.2rem;
  font-weight: normal; }

.ordering {
  width: 65.85366%;
  min-height: 55rem; }
  .ordering__tab {
    display: flex;
    max-width: 34rem; }
    .ordering__tab-btn {
      padding: 1rem 1.5rem;
      font-size: 14px;
      font-weight: bold;
      line-height: 1.286;
      background-color: transparent;
      color: #3882B9;
      text-align: left;
      border: 1px solid transparent; }
      .ordering__tab-btn.is-active {
        background-color: #fff;
        color: #000; }
      .ordering__tab-btn.is-active, .ordering__tab-btn:focus {
        border-color: #ccc #ccc transparent #ccc; }
      .ordering__tab-btn:hover {
        text-decoration: underline; }
      .ordering__tab-btn:focus {
        outline: none; }

.f-order__title--inline, .f-order__label {
  width: 15rem; }

.f-order {
  padding: 2.5rem; }
  .f-order__title--wide {
    margin: 0 0 2.5rem 0; }
  .f-order__block {
    margin-bottom: 3rem; }
  .f-order__item {
    display: flex;
    align-items: center;
    margin-bottom: 2rem; }
  .f-order__label {
    color: #8d8d8d;
    line-height: 1.125;
    cursor: pointer; }
  .f-order__input {
    padding: .3rem 1rem;
    transition: all .2s; }
    .f-order__input:focus::placeholder {
      color: #3882B9; }
  .f-order__select {
    padding: .5rem .8rem;
    appearance: none !important;
    background: url("../img/arrow.png") no-repeat;
    background-position: 98% center; }
    .f-order__select--lg {
      width: calc(100% - 15rem);
      max-width: 100%; }
  .f-order__textarea {
    display: block;
    padding: 1rem;
    margin: 0 0 2rem 15rem;
    font-size: 1.4rem; }

.tab-content {
  display: none; }
  .tab-content.is-active {
    display: block; }

.order, .order-wrap__total {
  padding: 1.5rem 2rem; }

.order {
  display: flex;
  align-items: center;
  border: 1px dotted;
  border-color: #ccc transparent; }
  .order__img {
    margin-right: 2rem; }
  .order__info {
    margin: 1rem 0 0 0;
    display: flex;
    justify-content: space-between;
    font-size: 1.4rem;
    line-height: 1.929; }
  .order__total {
    color: rgba(0, 0, 0, 0.6); }
  .order__price {
    font-weight: bold; }
  .order-wrap {
    width: 39rem;
    margin-left: 2rem; }
    .order-wrap__title {
      padding: 1.5rem 2rem; }
    .order-wrap__total {
      text-align: right;
      font-weight: bold; }

.btn-default {
  margin-left: 14.5rem;
  background-color: transparent;
  border: none; }
  .btn-default:focus {
    outline: none; }

.btn-apply {
  margin-left: 16rem;
  padding: 1rem 1.5rem;
  font-size: 2rem;
  font-weight: bold;
  border-radius: 90000rem;
  background-color: #5DBB1A;
  color: #fff;
  border: none;
  transition: box-shadow .2s, transform .2s; }
  .btn-apply:hover {
    box-shadow: 0 0 20px #5DBB1A;
    transform: scale(1.02); }

@media only screen and (max-width: 1024px) {
  .container {
    padding: 0 2rem; }
  .ordering {
